import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component:  () => import('@/Home.vue'),
      redirect: '/index',
      meta: {
        keepAlive: true
      },
      children: [
        {
          path: '/index',
          name: 'index',
          component: () => import('@/views/Index.vue'),
          meta: {
            keepAlive: true
          }
        },
        {
          path: '/exam',
          name: 'exam',
          component: () => import('@/views/Exam.vue'),
          meta: {
            keepAlive: true
          }
        },
        {
          path: '/subject',
          name: 'subject',
          component: () => import('@/views/Subject.vue'),
        },
        {
          path: '/user',
          name: 'user',
          component: () => import('@/views/User.vue'),
          meta: {
            keepAlive: true
          }
        }
      ]
    },
    {
      path: '/login',
      name: 'login',
      component: () => import('@/Login.vue'),
      meta: {
        anonymous: true
      }
    }
  ]
})
